@media screen and (max-width:768px) {
    .navbar-nav {
        flex-direction: row;
        gap: 1rem;
    }

    .logo {
        width: 9rem;
    }

    .follow-us-txt {
        color: var(--darkblue);
        font-size: 1rem;
        font-weight: 700;
        margin-top: 2px;
    }

    .social-icon {
        font-size: 1rem;
        font-weight: 700;
        color: var(--darkblue);
    }

    .banner-img {

        height: 50vh;
    }

    .input-wrapper {
        background-color: #ffffff;
        width: 100%;
        border-radius: 10px;
        /* height: 2.5rem; */
        padding: 5px 5px;
        border: 1px solid #4967A2;
        box-shadow: 0px 0px 18px #dddddd;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 20vh;
    }

    .select-city {
        width: 35%;
        font-size: 11px;
        font-weight: 400;
        border-left: none;
        border-right: 1px solid #000000;
        border-top: none;
        border-bottom: none;
        background-color: transparent;
    }

    .search-icon {
        color: var(--darkblue);
        font-size: 1rem;
        padding-left: 4px;
    }

    .search-input {
        background-color: transparent;
        border: none;
        height: 70%;
        padding: 1rem 0rem;
        font-size: 0.75rem;
        width: 100%;
        margin-left: 5px;
    }

    .search-btn {
        padding: 6px 13px;
        color: #ffffff;
        font-weight: 400;
        font-size: 1rem;
        background-color: var(--darkblue);
        border-radius: 10px;
        border: none;
    }

    .listingForm {
        flex-direction: row;
    }

    .main-section {
        display: block;
    }

    .main-side {
        width: 100%;

    }

    .region-name-li:not(:first-child) {
        margin-left: 5px;
    }

    .region-name-li {
        padding: 0px 7px;
    }

    .region-name-li a {
        font-size: 10px;
    }

    .property-region-name-li:not(:first-child) {
        margin-left: 10px;
    }

    .property-region-name-li {
        padding: 2px 5px;
    }

    .property-region-name-li a {
        font-size: 10px;
    }

    .mobile-property-details-view {
        display: block;
    }

    .luxury-homes-ul li {
        font-size: 10px;
        padding: 4px 5px;
    }

    .side-bar-icon-div svg {
        width: 30px;
        height: 30px;
        color: #000000;
        border: 1px solid var(--darkblue);
        background-color: #ffffff;
        border-radius: 50%;
        padding: 7px;
    }

    .sidebar-property-name-txt {
        font-size: 10px;
    }

    .sidebar-property-real-name-txt {
        font-size: 12px;
    }

    .possesion-date-main-div {
        display: flex;
        /* justify-content: space-around; */
        gap: 80px;
    }

    .download-brochure-btn {
        padding: 12px 20px;
        text-transform: uppercase;
        color: #ffffff;
        font-family: var(--opensans);
        font-weight: 700;
        background-color: var(--orange);
        border-radius: 100px;
        border: none;
        font-size: 14px;
        width: 100%;
    }

    .configuration-txt {
        font-size: 16px;
    }

    .project-overview-txt {
        font-size: 14px;
    }

    .config-main-price-div {
        display: flex;
        align-items: center;
        gap: 100px;
        margin-bottom: 10px;
    }

    .config-sqft-div div p {
        margin-bottom: 0px !important;
        font-size: 15px;
    }

    .sqft-txt {
        color: #808080;
        font-size: 12px !important;
    }

    .config-price-div div p {
        font-size: 14px;
    }

    .floor-plan-ul {
        gap: 10px;
    }

    .floor-plan-bhk-txt {
        padding: 5px 25px;
        font-size: 12px;
    }

    .floor-plan-bhk-txt span {
        color: #000000;
        font-size: 10px;
        font-weight: 300;
    }

    .amenities {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(90px, 1fr));
        gap: 6px;
        justify-items: center;
        align-items: center;
    }

    .amenity-item svg {
        width: 40px;
        /* Adjust as needed */
        height: 40px;
        color: var(--orange);
        background-color: #E7EDF8;
        /* Light background for circular icon */
        border-radius: 50%;
        padding: 5px;
    }

    .amenity-item p {
        font-size: 10px;
    }

    .image-gallery {
        gap: 10px;
        justify-content: start;
        padding: 10px;
        overflow: auto;
    }

    .side-bar {
        display: none;
    }

    .search-result-div {
        background-color: #ffffff;
        box-shadow: 0px 31px 157px rgba(0, 0, 0, 0.036), 0px 9.34559px 47.3309px rgba(0, 0, 0, 0.042), 0px 3.88168px 19.6588px rgba(0, 0, 0, 0.065), 0px 1.40393px 7.11022px rgba(0, 0, 0, 0.071);
        max-height: 310px;
        border-radius: 10px;
        width: 100%;
        text-align: center;
        overflow-x: hidden;
        margin-left: 0%;
        z-index: 9;
    }

    .region-name-div-for-homepage {
        padding: 10px 15px;
    }

    .regiona-name-txt {
        color: #ffffff;
        margin-bottom: 0px;
        font-family: var(--opensans);
        font-size: 16px;
        font-weight: 400;
    }

    .top-projects-txt {
        font-size: 12px;
    }

    .property-name-txt-inner-for-region {
        font-size: 12px;
    }

    .property-price-range-region-vise {
        font-size: 12px;
    }

    .view-more-link-by-subregion {
        margin-bottom: 0px;
        text-align: end;
        margin-top: 10px;
        font-size: 12px;
    }

    .view-more-link-by-subregion a {
        font-size: 12px;
        text-decoration: none;
        text-align: end;
        color: var(--darkblue);
    }

    .area-name-slider {
        padding: 10px !important;
    }

    .area-name-slider div {
        padding: 0px !important;
        font-size: 12px !important;
    }

    .subheading-txt {
        margin-top: 20px;
        margin-bottom: 0px;
        color: var(--orange);
        font-weight: 700;
        font-family: var(--opensans);
        font-size: 16px;
    }

    /* footer css  */
    .section-footer {
        position: relative !important;
        padding-top: 24px;
        padding-bottom: 24px;
        background-color: var(--darkblue);
        margin-top: 20px;
    }

    .footer-heading {
        font-size: 16px;
        font-family: var(--opensans);
        line-height: 31px;
        font-weight: 600;
        margin-bottom: 8px;
        margin-top: 15px;
        color: #ffffff;
    }

    .footer-heading-accordion {
        font-size: 16px;
    }

    .accordion-body p {
        font-size: 14px;
    }

    .copyright-footer-txt {
        font-size: 14px;
        text-align: start;
    }

    .design-footer-txt {
        font-size: 14px;
    }

    .contact-footer-txt {
        font-size: 14px;
    }

    .social-media-li a {
        color: #ffffff;
        margin-top: 10px;
        font-size: 14px;
        transition: all ease .4s;
    }

    .mobile-property {
        display: block;
        margin-bottom: 10px;
    }

    .property-devloper-name-txt {
        color: var(--orange);
        font-size: 16px;
        margin-bottom: 0px;
        white-space: nowrap;
        overflow: hidden;
        font-weight: 500;
        font-family: var(--opensans);
    }

    .devloper-name-property-inner-div {
        overflow: hidden;
    }

    .view-detail-btn-property {
        text-decoration: none;
        color: #000000;
        padding: 10px 10px;
        text-align: center;
        background-color: #eeeeee;
        font-size: 10px;
        font-weight: 500;
        font-family: var(--opensans);
        border-radius: 5px;
        border: none;
        /* width: 40%; */
        transition: all ease .4s;
    }

    .devloper-name-property-img {
        width: 150px;
        height: 150px;
        border-radius: 6px;
        object-fit: cover;
        display: block;
        /* align-self: flex-end; */
    }

    .get-call-btn-property-by-region {
        padding: 10px 10px;
        text-decoration: none;
        background-color: var(--darkblue);
        font-size: 10px;
        font-weight: 500;
        color: #ffffff;
        font-family: var(--opensans);
        transition: all ease .4s;
        border-radius: 5px;
        border: none;
        /* width: 60%; */
    }

    .filter-main-li:not(:first-child) {
        margin-left: 5px;
    }

    .filter-main-li {
        border: 1px solid var(--darkblue);
        border-radius: 40px;
        padding: 4px 10px;
        font-size: 10px;
        font-weight: 400;
        /* background-color: #E7EDF8; */
        color: #000000;
        font-family: var(--opensans);
    }

    .best-deal-txt {
        color: var(--orange);
        font-weight: 500;
        font-family: var(--opensans);
        font-size: 24px;
        margin-bottom: 0px;
    }

    .filter-main-div {
        margin-top: 5px;
    }

    .filter-main-ul {
        margin-bottom: 0px !important;
    }

    .best-deal-txt {
        color: var(--orange);
        font-weight: 500;
        font-family: var(--opensans);
        font-size: 20px;
        margin-bottom: 0px;
    }

    .home-page-devloper-txt {
        margin-top: 40px;
        margin-bottom: 0px;
        font-size: 14px;
    }

    .discounted-property-name {
        font-size: 16px;
    }

    .discounted-property-address-txt span {
        font-size: 14px;
    }

    .discounted-property-BHK-txt span {
        font-size: 14px;
    }

    .discounted-property-price span {
        font-size: 14px;
    }

    .view-detail-btn-property-home {
        text-decoration: none;
        color: #000000;
        padding: 10px 20px;
        background-color: #eeeeee;
        font-size: 14px;
        font-weight: 500;
        font-family: var(--opensans);
        border-radius: 5px;
        border: none;
        /* width: 45%; */
        transition: all ease .4s;
    }

    .get-call-btn-property-home-page {
        padding: 10px 20px;
        text-decoration: none;
        background-color: var(--darkblue);
        font-size: 14px;
        font-weight: 500;
        color: #ffffff;
        font-family: var(--opensans);
        transition: all ease .4s;
        border-radius: 5px;
        border: none;
    }

    .get-call-btn-property-devloper {
        padding: 10px 20px;
        text-decoration: none;
        background-color: var(--darkblue);
        font-size: 10px;
        font-weight: 500;
        color: #ffffff;
        font-family: var(--opensans);
        transition: all ease .4s;
        border-radius: 5px;
        border: none;
    }

    .best-deal-property-address-txt {
        margin-bottom: 0px;
        font-size: 15px;
        font-weight: 400;
        font-family: var(--opensans);
    }

    .property-detail-right-div {
        overflow: hidden;
    }

    .property-by-subregion-address-txt {
        margin-bottom: 0px;
        font-size: 15px;
        white-space: nowrap;
        overflow: hidden;
        font-weight: 400;
        font-family: var(--opensans);
    }

    .project-name-real {
        font-size: 14px;
    }

    .project-name-txt {
        margin-bottom: 0px;
        color: #151e2d;
        opacity: 0.6;
        font-size: 12px;
        text-align: start;
    }

    .customers-say-div {
        display: none;
    }

    .content-para {
        font-size: 13px;
        font-weight: 400;
        font-family: var(--opensans);
        color: #000000;
    }

    .review-user-name-txt {
        font-size: 14px;
    }

    .review-user-post-txt {
        font-size: 14px;
    }

    .review-user-icon {
        font-size: 45px;
        color: var(--darkblue);
        padding: 5px;
        border-radius: 50%;
        background-color: #ffffff;
    }

    .user-info-review-div {
        display: flex;
        gap: 20px;
        align-items: center;
    }




}