@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');

* {
  font-family: "Open Sans", serif;
}

:root {
  --opensans: "Open Sans", serif;
  --darkblue: #032B5F;
  --lightblue: rgba(3, 43, 95, 0.32);
  --orange: #DA361A;
  --lightorange: rgb(218, 54, 26, 0.32);
}

.navbar-bg {
  background-color: #E7EDF8;
}

.logo {
  width: 15rem;
  height: 2rem;
}

.follow-us-txt {
  color: var(--darkblue);
  font-size: 1.25rem;
  font-weight: 700;
}

.social-icon {
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--darkblue);
}

.banner-img {
  background-image: url(./assets/img/banner1.svg);
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  height: 90vh;

}

.input-wrapper {
  background-color: #ffffff;
  width: 100%;
  border-radius: 10px;
  /* height: 2.5rem; */
  padding: 5px 10px;
  border: 1px solid #4967A2;
  box-shadow: 0px 0px 18px #dddddd;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 30vh;
}

.select-city {
  width: 25%;
  font-size: 20px;
  font-weight: 400;
  border-left: none;
  border-right: 1px solid #000000;
  border-top: none;
  border-bottom: none;
  background-color: #ffffff;
}

.select-city:focus-visible {
  outline: none;
  border: none;

}

.search-input {
  background-color: transparent;
  border: none;
  height: 70%;
  padding: 1rem 0rem;
  font-size: 1rem;
  width: 100%;
  margin-left: 5px;

}

.search-btn {
  padding: 1rem 3rem;
  color: #ffffff;
  font-weight: 400;
  font-size: 1.125rem;
  background-color: var(--darkblue);
  border-radius: 10px;
  border: none;
}

.search-input:focus {
  outline: none;
}

.search-icon {
  color: var(--darkblue);
  font-size: 2rem;
  padding-left: 4px;
}

.sign-up-section {
  background-color: #d6d6d6;
  padding-bottom: 20px;
}

.create-listing-section {
  /* background-color: #d6d6d6; */
  /* height: 100vh; */
}

.sign-up-div {

  height: 100%;
  width: 100%;
}

.sign-up-txt {
  font-size: 30px;
  font-weight: 500;
}

.usernameinput {
  width: 100%;
  border-radius: 10px;
  border: none;
  padding: 10px 10px;
}

.usernameinput:focus {
  outline: none;
}

.sing-up-btn {
  background-color: var(--darkblue);
  color: #ffffff;
  width: 100%;
  padding: 15px;
  border: none;
  border-radius: 10px;
}

.create-listing-btn {
  background-color: var(--darkblue);
  color: #ffffff;
  width: 100%;
  padding: 15px;
  border: none;
  border-radius: 10px;
}


.create-listing-btn a {
  color: #ffffff;
  text-decoration: none;
}

.user-icon-p {
  text-align: center;
  color: var(--darkblue);
  font-size: 30px;
}

.user-icon {
  font-size: 30px;
  text-align: center;
}

.listingForm {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.listing-input {
  border: 1px solid var(--darkblue);
  border-radius: 10px;
  padding: 10px 10px;
  background-color: #ffffff;
}

.property-form-left-div {
  display: flex;
  flex-direction: column;
  gap: 10px;
  float: left;
}

.file-input-div {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.upload-btn {
  width: 150px;
  background-color: var(--darkblue);
  border: none;
  border-radius: 10px;
  color: #ffffff;
  text-transform: uppercase;
}

.create-listing-btn-div {
  /* width: 100%; */
  display: flex;

}

.create-property-btn {
  /* width: ; */
  background-color: var(--orange);
  border: none;
  border-radius: 10px;
  color: #ffffff;
  text-transform: uppercase;
  /* float: right; */
  padding: 10px;
}

.error-image-upload {
  color: var(--orange);
  font-weight: 700;
  text-align: left;
}

.listing-image {
  width: 100px;
  height: 100px;
  /* height: auto; */
  object-fit: contain;
  border-radius: 10px;
}

.delete-btn {
  background-color: rgb(243, 53, 53);
  color: #000000;
  font-weight: 400;
  width: 100px;
  padding: 10px;
  border: none;
  border-radius: 10px;
  text-transform: uppercase;
}

.listing-image-div {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.show-listing a {
  color: var(--orange);
}

.profile-page-properties-div {
  display: flex;
  width: 100%;
  justify-content: space-between;
  gap: 10px;
  align-items: center;
  border: 1px solid var(--darkblue);
  padding: 5px 5px;
  border-radius: 10px;
  margin-bottom: 10px;
}

.profile-property-name {
  text-align: start;
  color: #000000 !important;

}

.property-name-link-txt {
  text-decoration: none;
  color: #000000;
  font-weight: 500;

}

.property-name-link-txt:hover {
  text-decoration: underline;


}

.profile-page-property-img {
  height: 70px;
  width: 70px;
  border-radius: 10px;
  object-fit: contain;
}

.btn-delete {
  background-color: #d11a2a;
  padding: 5px 10px;
  border: none;
  border-radius: 10px;
  color: #ffffff;
}

.btn-update {
  background-color: var(--darkblue);
  padding: 5px 10px;
  border: none;
  border-radius: 10px;
  color: #ffffff;
}

.btn-delete:hover {
  background-color: hsla(355, 78%, 46%, 0.8);
  padding: 5px 10px;
  border: none;
  border-radius: 10px;
  color: #ffffff;
}

.btn-update:hover {
  background-color: hsla(214, 94%, 19%, 0.8);
  padding: 5px 10px;
  border: none;
  border-radius: 10px;
  color: #ffffff;
}

.property-btn-div {
  display: flex;
  float: right;
  align-items: flex-end;
  gap: 10px;
  justify-content: flex-end;
  /* width: 100%; */
}

/* property detail page css  */

.main-section {
  display: flex;
}

.main-side {
  width: 65%;

}

.region-name-div {
  margin-top: 10px;
}

.region-name-ul {
  display: flex;
  justify-content: start;
  align-items: center;
  list-style: none;

}

.region-name-ul {
  display: flex;
  justify-content: start;
  align-items: center;
  list-style: none;

  padding-left: 0px;
}

.property-region-name-ul {
  display: flex;
  padding-left: 0px;
  justify-content: start;
  align-items: center;
  list-style: none;
}

.region-name-li:not(:first-child) {
  margin-left: 15px;
}

.region-name-li {
  border: 1px solid var(--darkblue);
  border-radius: 4px;
  padding: 4px 10px;
  font-weight: 400;
  color: #808080;
  font-family: var(--opensans);
}

.region-name-li a {
  text-decoration: none;
  color: #808080;
}

.property-region-name-li:not(:first-child) {
  margin-left: 15px;
}

.property-region-name-li {
  border-bottom: 2px solid var(--darkblue);
  border-radius: 4px;
  padding: 4px 10px;
  font-weight: 400;
  background-color: #E7EDF8;
  color: #808080;
  font-family: var(--opensans);
}

.property-region-name-li a {
  color: #000000;
  font-weight: 400;
  text-decoration: none;
}

.main-property-img {
  height: 460px;
  object-fit: cover;
  width: 100%;
  border-radius: 10px;
}

.configuration-txt {
  color: #202020;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 20px;
  font-family: var(--opensans);
}

.config-main-price-div {
  display: flex;
  /* justify-content: center; */
  align-items: center;
  gap: 200px;
  margin-bottom: 10px;
}

.config-sqft-div {
  display: flex;
  align-items: center;
  gap: 10px;
}

.building-icon {
  color: #000000;
  font-size: 30px;
}

.sqft-txt {
  color: #808080;
  font-size: 12px;
}

.config-sqft-div div p {
  margin-bottom: 0px !important;
}

.bhk-txt {
  font-size: 17px;
  color: #000000;
}

.sqft-txt {
  color: #808080;
  font-size: 17px;
}

.config-price-div div p {
  font-size: 16px;
  margin-bottom: 0px !important;
  color: var(--orange);
  font-weight: 700;
}

.floor-plan-ul {
  display: flex;
  gap: 30px;
  padding-left: 0px !important;
  justify-content: start;
  align-items: center;
  list-style: none;
}

.floor-plan-bhk-txt {
  padding: 10px 30px;
  text-align: center;
  background-color: #E7EDF8;
  color: #000000;
  font-weight: 500;
  border: 1px solid var(--darkblue);
  border-radius: 50px;
  font-size: 14px;
  transition: all .4s;
}

.floor-plan-bhk-txt:hover {
  cursor: pointer;
}

.floor-plan-bhk-txt span {
  color: #000000;
  font-size: 12px;
  font-weight: 300;
}

.project-overview-txt {
  color: #3B3B3B;
  font-size: 15px;
  font-family: var(--opensans);
}

.amenities {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 20px;
  justify-items: center;
  align-items: center;
}

.amenity-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.amenity-item svg {
  width: 50px;
  /* Adjust as needed */
  height: 50px;
  color: var(--orange);
  background-color: #E7EDF8;
  /* Light background for circular icon */
  border-radius: 50%;
  padding: 10px;
}

.amenity-item p {
  margin-top: 8px;
  font-size: 13px;
  font-weight: 500;
  color: #202020;
}

.image-gallery {
  display: flex;
  gap: 20px;
  justify-content: start;
  padding: 20px;
  overflow: auto;
  scrollbar-width: none;
  scrollbar-color: #888 #e0e0e0;
}

.image-item img {
  width: 150px;
  /* Set width as needed */
  height: 200px;
  /* Set height as needed */
  object-fit: cover;
  border-radius: 15px;
  /* Rounded corners */
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  /* Optional shadow for effect */
}

.side-bar {
  background-color: #ffffff;
  width: 35%;
  height: 100%;

  position: sticky;
  overflow: hidden;
  top: 15%;
}

.property-details-div {
  padding: 10px;
  background-color: #E7EDF8;
  border-radius: 10px;
  margin-top: 15px;
}

.luxury-homes-ul {
  display: flex;
  list-style: none;
  padding-left: 0px;
}

.luxury-homes-ul li {
  border-radius: 50px;
  text-transform: uppercase;
  color: #ffffff;
  background-color: var(--darkblue);
  font-weight: 500;
  padding: 4px 9px;
}

.side-bar-main-address-div {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 20px;
}

.side-bar-icon-div svg {
  width: 40px;
  height: 40px;
  color: #000000;
  border: 1px solid var(--darkblue);
  background-color: #ffffff;
  border-radius: 50%;
  padding: 10px;
}

.side-bar-property-detail-div p {
  margin-bottom: 0px;
}

.sidebar-property-name-txt {
  color: #505050;
  font-family: var(--opensans);
  font-weight: 400;
  font-size: 12px;
}

.sidebar-property-real-name-txt {
  color: #202020;
  font-size: 16px;
  font-weight: 500;
}

.possesion-date-main-div {
  display: flex;
  /* justify-content: space-around; */
  gap: 60px;
}

.download-brochure-btn {
  padding: 12px 20px;
  text-transform: uppercase;
  color: #ffffff;
  font-family: var(--opensans);
  font-weight: 700;
  background-color: var(--orange);
  border-radius: 100px;
  border: none;
  font-size: 16px;
  width: 100%;
}

.form-enquire-btn {
  padding: 12px 20px;
  text-transform: uppercase;
  color: #ffffff;
  font-family: var(--opensans);
  font-weight: 700;
  background-color: var(--orange);
  border-radius: 10px;
  border: none;
  font-size: 16px;
  width: 100%;
}

.main-form-div {
  margin-top: 30px;
  background-color: #E7EDF8;
  padding: 20px;
  border-radius: 10px;
  margin-bottom: 20px;
}

.need-help-txt {
  text-transform: uppercase;
  font-weight: 500;
  font-family: var(--opensans);
  font-size: 24px;
}

.lead-form-div {
  border-radius: 10px;
  background-color: #ffffff;
  padding: 20px 10px;
}

.lead-form-input {
  border-radius: 6px;
  border: 1px solid #808080;
  background-color: #F5F5F5;
  width: 100%;
  margin-bottom: 20px;
  font-family: var(--opensans);
  font-size: 14px;
  font-weight: 400;
  padding: 15px 10px;
}

/* footer css  */
.section-footer {
  position: relative !important;
  padding-top: 24px;
  padding-bottom: 24px;
  background-color: var(--darkblue);
  margin-top: 30px;
}

.footer-video {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top right;
  opacity: 1;
  z-index: -1;
}

.footer-heading {
  font-size: 22px;
  font-family: var(--opensans);
  line-height: 31px;
  font-weight: 600;
  margin-bottom: 10px;
  margin-top: 20px;
  color: #ffffff;
}

.footer-heading-accordion {
  font-size: 22px;
  font-family: var(--opensans);
  line-height: 31px;
  font-weight: 600;
  margin-bottom: 10px;
  /* margin-top: 20px; */
  color: #ffffff;
}

.accordion-button:focus {
  box-shadow: none !important;

}

.social-media-ul {
  display: flex;
  padding-left: 0px;
  align-items: center;
  font-size: 20px;
  justify-content: start;
  list-style: none;
  color: #ffffff;
}

.social-media-li a {
  color: #ffffff;
  margin-top: 10px;
  font-size: 18px;
  transition: all ease .4s;
}

.social-media-li a:hover {
  color: var(--orange);
}

.contact-footer-txt {
  color: #ffffff;
  margin-bottom: 10px;
}

.copyright-footer-txt {
  color: #ffffff;
  margin-bottom: 10px;
  text-align: start;
}

.design-footer-txt {
  color: #ffffff;
  margin-bottom: 10px;
  text-align: end;
}



.accordion {
  background-color: var(--darkblue) !important;
  --bs-accordion-bg: var(--darkblue);
  --bs-accordion-btn-padding-x: 0px !important;
  --bs-accordion-active-bg: var(--darkblue);
}

.design-footer-txt a {
  color: #ffffff;
  margin-bottom: 10px;
  text-align: end;
}

.accordion-body p {
  color: #ffffff !important;
}

.contact-footer-txt a {
  color: #ffffff;
  margin-left: 10px;
  text-decoration: none;
  margin-bottom: 10px;
}

.social-media-li:not(:first-child) {
  margin-left: 30px;
}

.footer-menu {
  list-style: none;
  padding-left: 0px !important;
  font-size: 14px;
  font-family: var(--opensans);
  font-weight: 400;
  line-height: 18px;
}

.footer-menu li {
  margin-bottom: 10px;
}

.footer-menu li a {
  text-decoration: none;
  color: #ffffff;
  font-weight: 400;
  font-family: var(--opensans);
  font-size: 14px;
}

.footer-address {
  font-size: 16px;
  font-family: var(--opensans);
  font-weight: 400;
  line-height: 18px;

}

.footer-contact {
  font-family: var(--opensans);
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #ffffff;
  text-decoration: none;
}

.address-color {
  color: #ffffff;
}

.footer-icon {
  color: #ffffff;
}

